import styled from 'styled-components';
import {motion} from 'framer-motion'

export const HeaderNav = styled(motion.div)`
    height: 0px;
    width: 100%;
    position: absolute;
    top: 72px;
    right: 0;
    left: 0%;
    z-index: 99;
`
export const Logo = styled.div`

    img {
        width: 40px;
        filter: ${props => props.theme.filterToDefaultBlack};
    }

    a {
        font-size: 1.8rem;
        font-weight: 700;
        color: ${props => props.theme.text}
    }


    span {
        cursor: pointer;
        height: 1rem;
        width: 1rem;
        background: ${props => props.theme.red};
        margin: 0 5px;
        border-radius: 100%;
        display: inline-block;
        position: relative;
        bottom: 0px;
    }
`
export const Menu = styled.div`

    

    transform-origin: center;
    /* TODO: Check this */
    border: none;
    background: none;
    outline: none;
    
    /* padding: 20px; */
    
    

    button {
        border: none;
        background: none;
        outline: none;
        &:hover {
            transition: 0.3s ease;
            cursor: pointer;

            span {
                transition: 0.3s ease;

                &:first-child {
                    width: 26px;
                    
                } 
            }
        }

        

    }

    span {
        width: 36px;
        height: 4px;
        display: block;
        background: ${props => props.theme.text};
        
        /* margin: 8px; */

        &:first-child {
            margin-bottom: 8px;
        }

        
    }
`